import { _decorator, Component, instantiate, log, Node } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('bg')
export class b_g extends Component {
    n1: Node;
    n2: Node;
    w: number;
    @property({
        tooltip:"向左移动速度",
        range:[0.01,10]
    })
    speed:number = 0;
    protected onLoad(): void {
        let num = this.node.children.length;
        if(num!=1){
            log("子节点循环移动只允许只有一个子节点");
            return
        }

        let n1 = this.node.children[0];
        let n2 = instantiate(n1);
        n2.parent = this.node;

        n2.setPosition({...n2.position, y: n1.height})
        
        this.n1 = n1;
        this.n2 = n2;
        this.w = n1.height;
    }
    start() {

    }

    update(deltaTime: number) {
        this.node.setPosition({
            ...this.node.position,
            y: this.node.position.y - 2,
        })
    }
}

